import { Component } from "react";
import SideBar from "../../../layout/Profile-side-bar/SideBar"
import { Col, Card, Figure, Container } from 'react-bootstrap'


class Favorites extends Component {

    render() {

        return (

            <>
                <SideBar loggedUser={this.props.loggedUser} {...this.props} />
                <Container>
                    <Col md={3}>
                        <Card className='convocations-card' style={{ width: '18rem' }}>
                            <Card.Body>
                                <Figure>
                                    <Figure.Image
                                        width={171}
                                        height={180}
                                        alt="171x180"
                                        src=''
                                    />
                                </Figure>
                                <Card.Title>Name</Card.Title>
                                <Card.Text></Card.Text>
                            </Card.Body>
                        </Card>
                    </Col>
                </Container>
            </>
        )
    }
}

export default Favorites